const SearchBar = ({ handleResultSelect }) => {
    console.log('rendering');
    const [searchTerm, setSearchTerm] = useState('');
    const [results, setResults] = useState([]);
    const [isSearching, setIsSearching] = useState(false);
    const debouncedSearchTerm = useDebounce(searchTerm, 500);

    async function fetch() {
        const response = await getUsernames(debouncedSearchTerm);
        const formattedResults = response.map((result, index) => {
            result.key = index;
            result.title = result.username;
            return result;
        });
        setResults(formattedResults);
        return formattedResults;
    }

    useEffect(
        () => {
            if (debouncedSearchTerm) {
                setIsSearching(true);
                console.log(isSearching);
                setResults(fetch());
                setIsSearching(false);
                // const results = await getUsernames(debouncedSearchTerm);
                // const formattedResults = results.map((result, index) => {
                //     result.key = index;
                //     result.title = result.username;
                //     return result;
                // });
            } else {
                setResults([]);
                // resetComponent();
            }
        },
        [debouncedSearchTerm] // Only call effect if debounced search term changes
    );

    const resetComponent = () => {
        setSearchTerm('');
        setIsSearching(false);
    };

    const onResultSelect = (e, { result }) => {
        setSearchTerm(result.username);
        handleResultSelect(result.username);
        setSearchTerm('');
    };

    const onSearchChange = (e, { value }) => {
        setIsSearching(true);
        setSearchTerm(value);
    };

    return (
        <Search
            input={{ fluid: true, icon: 'arrow circle right' }}
            loading={isSearching}
            placeholder={'Enter your Epic username'}
            onResultSelect={onResultSelect}
            onSearchChange={onSearchChange}
            results={results}
            resultRenderer={resultRenderer}
            value={searchTerm}
            showNoResults={false}
        />
    );
};

export default SearchBar;

